<template>
  <section>
    <v-touch v-on:swiperight="onSwipeRight" class="touchWapper" :swipe-options="{direction: 'horizontal'}">
    <!---->
    <com_header :headerTitle="headerTitle"></com_header>
    <div class="c-container">
      <van-cell-group class="my-info-list">
        <van-cell title=""  @click="openPage('/Mine/myData')">
          <template >
            <img class="user-img" src="../../assets/images/head_img.jpg" >
          </template>
          <!--<van-icon slot="right-icon" name="friends" class="custom-icon" />-->
        </van-cell>
        <van-cell title=""  value="00001" >
        </van-cell>
        <van-cell title=""   is-link>
        </van-cell>
        <van-cell title=""   is-link>
        </van-cell>
        <van-cell title=""  is-link @click="clearCache()">
        </van-cell>
        <van-cell title="" value="0.1.1"  is-link>
        </van-cell>
      </van-cell-group>
        <van-button  class="button-div mt20" plain round  type="primary" @click="loginOut"></van-button>
    </div>
    </v-touch>
  </section>

</template>
<style scoped lang="scss">
  .my-info-list{
    margin-top: 10px;
      .h-title{
        font-size: 14px;
      }
  }
  .user-img {
    width: 30px;
    height: 30px;
    border-radius: 30px;

    /*border: 2px solid #ffffffe0;*/
  }
  .van-icon{
    font-size: 20px;
  }
  .button-div{
    width: 80%;
    margin-left: 10%;
    height: 30px;
    line-height: 30px;
    color: #28c88c;
    border: 1px solid #28c88c;
  }
</style>
<script>
// jquery
// import * as indexManageApi from '../../api/main';
import comHeader from '../components/header';
import { Dialog } from 'vant';

export default {
  // inject: ['reload'],
  data () {
    return {
      headerTitle: '',
      dataList: []
    };
  },
  components: {
    com_header: comHeader
  },
  created () {
    this.loadData();
  },

  methods: {
    loadData () {
      this.dataList = [
        {id: 1, title: ' ', value: ''}];
    },
    openPage (to) {
      this.$router.push({path: to});
    },
    // 
    clearCache () {
      Dialog.confirm({
        title: '',
        message: ''
      }).then(() => {
        // on confirm
      }).catch(() => {
        // on cancel
      });
    },
    loginOut () {
      this.$store.commit('$_removeStorage');
      this.$router.push('/login');
    }
  },
  mounted () {
  }
};

</script>



