<template>
  <div class="date">
    <!--   -->
    <!--<div class="month">-->
    <!--<p>{{ currentYear }}{{ currentMonth }}</p>-->
    <!--</div>-->
    <!--  -->
    <ul class="weekdays index-list-wrap">
      <li></li>
      <li></li>
      <li></li>
      <li></li>
      <li></li>
      <li></li>
      <li></li>
    </ul>
    <!--  currentWeekNo 0-->
    <!--<van-swipe @change="onChange" style="height:80px; " :initial-swipe="currentWeekNo-1" :show-indicators="false">-->
      <!--<van-swipe-item v-for="n in currentWeekCount" :key="n">-->
        <ul class="days" >
          <li @click="pick(day)" v-for="(day, index) in days" :key="index">
            <!---->
            <!--<span v-if="day.getMonth()+1 != currentMonth" class="other-month">{{ day.getDate() }}-->
        <!--</span>-->
            <!--<span v-else>-->
          <!---->
          <!--<span-->
            <!--v-if="day.getFullYear() == new Date().getFullYear() && day.getMonth() == new Date().getMonth() && day.getDate() == new Date().getDate()"-->
            <!--class="active">{{ day.getDate() }}</span>-->
                 <!--<span v-else>{{ day.getDate() }}</span>-->
            <!--<i class="iconfont icon-yuandianxiao yuandian-icon"></i>-->
              <!--&lt;!&ndash;<i class="iconfont icon-hanghou- feiji-icon"></i>&ndash;&gt;-->
          <!--</span>-->
                 <span :class="currentShow==index?'active':''">
                   {{ day.getDate() }}
                 <i class="iconfont icon-yuandianxiao yuandian-icon" v-if="index==5"></i>
                 </span>
            <!--</span>-->

          </li>
        </ul>
      <!--</van-swipe-item>-->
    <!--</van-swipe>-->
  </div>
</template>

<script>

// import {Toast} from 'vant';
// import { calendarMsgService } from './CalendarMsgService';
export default {
  name: 'date',
  props: {
    currentShow: {default: 1}
  },
  data () {
    return {
      currentYear: 2019, // 
      currentMonth: 1, // 
      currentDay: 1, // 
      currentWeek: 1, // 
      currentWeekNo: 1, // 
      currentWeekCount: 5, // 
      days: [],
      currentDate: new Date()
    };
  },
  mounted () {
  },
  created () {
    this.initData(null);
    var d = new Date();
    // this.currentShow = d.getDay() - 1;
    if (d.getDay() === 0) {
      this.currentShow = 6;
    } else {
      this.currentShow = d.getDay() - 1;
    }
  },
  methods: {
    formatDate (year, month, day) {
      const y = year;
      let m = month;
      if (m < 10) m = `0${m}`;
      let d = day;
      if (d < 10) d = `0${d}`;
      return `${y}-${m}-${d}`;
    },

    initData (cur) {
      let date = '';
      if (cur) {
        date = new Date(cur);
      } else {
        date = new Date();
      }
      this.currentDay = date.getDate(); //  
      this.currentYear = date.getFullYear(); // 
      this.currentMonth = date.getMonth() + 1; // 
      this.currentWeek = date.getDay(); // 1...6,0   // 
      if (this.currentWeek === 0) {
        this.currentWeek = 7;
      }
      const str = this.formatDate(this.currentYear, this.currentMonth, this.currentDay);//  --

      var weekConfig = this.getMonthWeek(this.currentYear, this.currentMonth, this.currentDay);
      this.currentWeekNo = weekConfig.getWeek;
      this.currentWeekCount = weekConfig.getWeekCount;
      this.days.length = 0;
      // 76  i<= 35- this.currentWeek
      /* eslint-disabled */
      for (let i = this.currentWeek - 1; i >= 0; i -= 1) {
        const d = new Date(str);
        d.setDate(d.getDate() - i);
        // console.log(y:" + d.getDate())
        this.days.push(d);
      }
      for (let i = 1; i <= 7 - this.currentWeek; i += 1) {
        const d = new Date(str);
        d.setDate(d.getDate() + i);
        this.days.push(d);
      }
    },

    // //  
    // weekPre () {
    //   const d = this.days[0]; // 77
    //   d.setDate(d.getDate() - 7);
    //   this.initData(d);
    // },
    //
    // //  
    // weekNext () {
    //   const d = this.days[6]; // 77
    //   d.setDate(d.getDate() + 7);
    //   this.initData(d);
    // },
    //  
    dayPre () {
      var preDate = new Date(this.currentDate.getTime() - 24 * 60 * 60 * 1000); // 
      this.currentShow = preDate.getDay() - 1;
      this.currentDate = preDate;
      // if (this.currentShow > 1) {
      //   this.currentShow = this.currentShow - 1;
      // } else {
      //   this.currentShow = 6;
      // }
    },

    // 
    dayNext () {
      var nextDate = new Date(this.currentDate.getTime() + 24 * 60 * 60 * 1000); // 
      // this.currentShow = nextDate.getDay() - 1;
      if (nextDate.getDay() === 0) {
        this.currentShow = 6;
      } else {
        this.currentShow = nextDate.getDay() - 1;
      }
      this.currentDate = nextDate;
      // if (this.currentShow < 6) {
      //   this.currentShow = this.currentShow + 1;
      // } else {
      //   this.currentShow = 0;
      // }
    },

    // //    
    // pickPre (year, month) {
    //   const d = new Date(this.formatDate(year, month, 1));
    //   d.setDate(0);
    //   this.initData(this.formatDate(d.getFullYear(), d.getMonth() + 1, 1));
    // },
    // //    
    // pickNext (year, month) {
    //   const d = new Date(this.formatDate(year, month, 1));
    //   d.setDate(35);
    //   this.initData(this.formatDate(d.getFullYear(), d.getMonth() + 1, 1));
    // },

    // 
    pick (date) {
      // alert(this.formatDate(date.getFullYear(), date.getMonth() + 1, date.getDate()));
      this.currentShow = date.getDay() - 1;
      this.currentDate = date;
      this.$emit('pickDate', this.currentDate);
    },
    onChange (index) {
      // Toast(' Swipe ' + index);
      if (index < this.currentWeekNo - 1) {
        this.weekPre();
      } else {
        this.weekNext();
      }
    },
    getMonthWeek (a, b, c) {
      /* a = d = 
                   * b = 6 - w = ()
                    * a + b 7 
                   */
      var mdate = new Date(a, parseInt(b) - 1, c);
      var w = mdate.getDay();
      var d = mdate.getDate();
      if (w === 0) {
        w = 7;
      }
      var config = {
        getMonth: mdate.getMonth() + 1,
        getYear: mdate.getFullYear(),
        getWeek: Math.ceil((d + 6 - w) / 7),
        getWeekCount: Math.ceil((d + 6) / 7)
      };
      return config;
    }
    // var getMonthWeek = function (a, b, c) {
    //         /**
    //           * a = d = 
    //           * b = 6 - w = ()
    //            * a + b 7 
    //           */
    //           var date = new Date(a, parseInt(b) - 1, c),
    //                w = date.getDay(),
    //                 d = date.getDate();
    //           if(w==0){
    //                w=7;
    //             }
    //           var config={
    //              getMonth:date.getMonth()+1,
    //                  getYear:date.getFullYear(),
    //               getWeek:Math.ceil((d + 6 - w) / 7),
    //              }
    //            return config;
    //      };
    // var getDate=getMonthWeek("2018", "12", "31");
  }
};

</script>

<style scoped lang="scss">
  .date {
    height: 90px;
    color: #333;

    .month {
      font-size: 14px;
      text-align: center;
      margin-top: 10px;
    }

    .weekdays {
      display: flex;
      font-size: 14px;
      margin-top: 10px;
      color: #999;

      li {
        flex: 1;
        text-align: center;
      }
    }

    .index-list-wrap {
      width: 100%;
      overflow: auto;
      -webkit-overflow-scrolling: touch;
    }
    .days {
      display: flex;
      margin-top: 10px;
      margin-bottom: 10px;
      color: #666;

      li {
        flex: 1;
        font-size: 14px;
        text-align: center;
        margin-top: 5px;
        line-height: 32px;
        position: relative;

        .active {
          display: inline-block;
          width: 32px;
          height: 32px;
          text-align: center;
          color: #fff;
          border-radius: 50%;
          margin-top: 3px;
          background-color: #26c88c;
        }
        .yuandian-icon {
          position: absolute;
          /*top: 0px;*/
          /*left: 18px;*/
          bottom: -2px;
          left: 19px;
          line-height: 14px;
          color: #26c88c;
          font-size: 18px;
        }
        /*.feiji-icon{*/
        /*position: absolute;*/
        /*bottom: 0px;*/
        /*left: 19px;*/
        /*line-height: 14px;*/
        /*color:#999;*/
        /*}*/
        .yuandian-icon-blue {
          color: #26c88c;
        }
        .yuandian-icon-red {
          color: #ff0000;
        }

        .other-month {
          color: #999;
        }
      }
    }
  }
</style>



