<template>
  <div>

    <div class="date">
      <!--   -->
      <div class="month">
      <i class="iconfont icon-jiantouleft last-month" @click="pickPre(currentYear)"></i>
      <span>{{ currentYear }}  </span>
      <i class="iconfont icon-jiantouright next-month" @click="pickNext(currentYear,currentMonth)"></i>
      </div>
      <!--  -->
      <ul class="listdays">
        <van-button round size="small"></van-button>
        <van-button round size="small"></van-button>
        <van-button round size="small"></van-button>
      </ul>
      <div class="bodyDiv">
        <ul class="days" >
          <li @click="pick()" >
            <span>1</span>
            <span>2</span>
            <span>3</span>
            <span>4</span>
            <span>5</span>
            <span>6</span>
            <span>7</span>
            <span>8</span>
            <span>9</span>
            <span>10</span>
            <span>11</span>
            <span>12</span>
          </li>
        </ul>
      </div>
    </div>
    <!--<van-button type="primary" @click="returnNow()"></van-button>-->
    <!--<van-button type="primary"  @click="setRestOrWork('R')"></van-button>-->
    <!--<van-button type="primary" @click="setRestOrWork('W')"></van-button>-->
    <!--<van-button type="primary"  @click="cancel()"></van-button>-->
    </div>
</template>

<script>
// import { calendarMsgService } from './CalendarMsgService';
import { Dialog } from 'vant';
export default {
  name: 'date',
  props: {
    currentYear: {
      default: 2018
    },
    currentMonth: {
      default: 12
    }
    // flyDaysList: {
    //   default: []
    // }
  },
  data () {
    return {
      // currentYear: 1970, // 
      // currentMonth: 1, // 
      currentDay: 1, // 
      currentWeek: 1, // 
      firstWeek: 1,
      days: [],
      daysUL: [],
      params: {
        selectDay: '',
        type: ''
      },
      isSelected: [],
      isBan: [],
      isFly: [],
      isNormal: [],
      restDays: {
        year: '',
        month: '',
        day: '',
        resttype: '',
        restdate: ''
      },
      // flyDaysList: [],
      banList: [],
      xiuList: [],
      selectIndex: ''
    };
  },

  created () {
    this.initData(null);
    // var flyDaysList = [{resttype: 'R', day: 2, isNormal: 'Y'},
    //   {resttype: 'R', day: 10, isNormal: 'Y'},
    //   {resttype: 'R', day: 11, isNormal: 'Y'},
    //   {resttype: 'R', day: 12, isNormal: 'N'},
    //   {resttype: 'R', day: 14, isNormal: 'N'},
    //   {resttype: 'R', day: 22, isNormal: 'Y'},
    //   {resttype: 'R', day: 24, isNormal: 'Y'}];
    // this.dealResult('2019', '01', flyDaysList);
    this.dealResult('2019', '01', []);
  },

  methods: {
    // 
    formatDate (year, month, day) {
      const y = year;
      let m = month;
      if (m < 10) m = `0${m}`;
      let d = day;
      if (d < 10) d = `0${d}`;
      return `${y}/${m}/${d}`;
    },

    initData (cur) {
      let date = '';
      if (cur) {
        date = new Date(cur);
      } else {
        date = new Date();
      }
      this.currentDay = date.getDate(); //  
      this.currentYear = date.getFullYear(); // 
      this.currentMonth = date.getMonth() + 1; // 
      this.currentWeek = date.getDay(); // 1...6,0   // 

      // 
      date.setDate(1);
      this.firstWeek = date.getDay();

      if (this.firstWeek === 0) {
        this.firstWeek = 7;
      }
      const str = this.formatDate(this.currentYear, this.currentMonth, 1);//  --
      this.days.length = 0;

      // 76  i<= 42- this.firstWeek
      for (let i = this.firstWeek - 1; i >= 0; i -= 1) {
        const d = new Date(str);
        d.setDate(d.getDate() - i);
        this.days.push(d);
      }
      // 1 7  7daysUL
      if (this.days.length % 7 === 0) {
        this.daysUL.push(this.days);
        this.days = [];
      }

      for (let i = 1; i <= 42 - this.firstWeek; i += 1) {
        const d = new Date(str);
        d.setDate(d.getDate() + i);
        this.days.push(d); //  days 7  daysUL days  42
        if (this.days.length % 7 === 0) {
          this.daysUL.push(this.days);
          this.days = []; // 
        }
      }
      //  
      // calendarMsgService.getRestDays({currentYear: this.currentYear, currentMonth: this.currentMonth}).then(res => {
      //   if (res.code === 0) {
      //     this.flyDaysList = res.content;
      //     this.dealResult(this.currentYear, this.currentMonth);
      //   }
      // });
    },
    setRestOrWork (type) {
      if (this.onlySelect()) {
        this.params.type = type;
        this.isFly[this.selectIndex] = true;
        if (type === 'R') {
          this.isFly[this.selectIndex] = true;
        }
        if (type === 'W') {
          this.isBan[this.selectIndex] = true;
        }
        this.params.selectDay = '';
        this.params.type = '';
        // calendarMsgService.addRestDays(this.params).then(res => {
        //   if (res.code === 0) {
        //     this.$message({
        //       message: '',
        //       type: 'success'
        //     });
        //     if (type === 'R') {
        //       this.isFly[this.selectIndex] = true;
        //     }
        //     if (type === 'W') {
        //       this.isBan[this.selectIndex] = true;
        //     }
        //   } else {
        //     this.$message({
        //       message: res.msg,
        //       type: 'error'
        //     });
        //   }
        //   this.params.selectDay = '';
        //   this.params.type = '';
        // });
      }
    },
    cancel () {
      if (this.onlySelect()) {
      }
    },

    // 
    dealResult (currentYear, currentMonth, flyDaysList) {
      this.banList = []; //   
      this.xiuList = []; //   
      this.isBan = []; // 
      this.isFly = [];
      let zhouji = new Date(this.formatDate(currentYear, currentMonth, 1)).getDay(); //  1 
      if (zhouji === 0) { // 0 
        zhouji = 7;
      }
      for (let i = 0; i < flyDaysList.length; i++) {
        this.restDays = flyDaysList[i];
        if (this.restDays.resttype === 'W') {
          let ban = this.restDays.day - 1 + (zhouji - 1);// 
          this.banList.push(ban);
        }
        if (this.restDays.resttype === 'R') {
          let flagDay = this.restDays.day - 1 + (zhouji - 1);// 
          // this.xiuList.push(xiu);
          var xiuObj = {flag: flagDay, isNormal: this.restDays.isNormal};
          this.xiuList.push(xiuObj);
        }
      }
      for (let m = 0; m < 42; m++) { // banlist 
        let nothave = true; // 
        for (let k = 0; k < this.banList.length; k++) {
          if (m === this.banList[k]) {
            this.isBan.push(true);
            nothave = false;
            break;
          }
        }
        if (nothave) {
          this.isBan.push(false);
        }
      }
      for (let n = 0; n < 42; n++) { // 
        let nothave = true;
        for (let k = 0; k < this.xiuList.length; k++) {
          if (n === this.xiuList[k].flag) {
            var isFlyObj = {flag: true, isNormal: this.xiuList[k].isNormal};
            this.isFly.push(isFlyObj);
            nothave = false;
            break;
          }
        }
        if (nothave) {
          var isFlyObj1 = {flag: false};
          this.isFly.push(isFlyObj1);
          // this.isFly.push(false);
        }
      }
    },
    returnNow () {
      this.daysUL = [];
      this.initData(null);
    },
    // 
    pick (date, index) {
      this.selectIndex = index;
      this.isSelected = [];
      this.params.selectDay = this.formatDate(date.getFullYear(), date.getMonth() + 1, date.getDate());
      for (let i = 0; i < 42; i++) {
        if (index === i) {
          this.isSelected.push(true);
          continue;
        }
        this.isSelected.push(false);
      }
    },
    onlySelect () {
      if (this.params.selectDay === '') {
        Dialog.alert({
          message: ''
        }).then(() => {
          // on close
        });
        // this.$message({
        //   message: '',
        //   type: 'warning'
        // });
        return false;
      }
      return true;
    },
    //    
    pickPre (year) {
      this.currentYear = this.currentYear - 1;
    },
    //    
    pickNext (year) {
      this.currentYear = this.currentYear + 1;
    }
  }
};

</script>

<style scoped  lang="scss">
  .date {
    height: 180px;
    width:100%;
    color: #333;
    /*background: #fff;*/
  }

  .button>div{
    margin-top:10px;

  }
  .month {
    font-size: 16px;
    text-align: center;
    height: 40px;
    line-height: 40px;
    position: relative;
    .last-month{
      position: absolute;
      left: 10px;
      margin-right: 0;
    }
    .next-month{
      position: absolute;
      right: 10px;
      margin-right: 0;
    }
  }
  .listdays {
    /*background-color: #0096ff;*/
    /*opacity: 0.6;*/
    display: flex;
    font-size: 14px;
    height: 40px;
    line-height: 40px;
    color: #444;
    border-bottom: 1px solid #d5d5d5
  }
  .listdays button{
    margin-left: 13%;
  }

  .days {
    display: flex;
  }

  li {
    flex: 1;
    font-size: 14px;
    width:13%;
    list-style-type:none;
    text-align: left;
    margin-top: 5px;
    line-height:  40px;
    cursor:pointer;
    position: relative;
    > span{
      display: inline-block;
      width: 50px;
      height: 40px;
      border-radius: 50%;
      background: #fff;
      text-align: center;
    }
    .yuandian-icon{
      position: absolute;
      top: 0px;
      left: 18px;
      line-height: 14px;
      color: #ff0000;
      font-size: 18px;
    }
    .feiji-icon{
      position: absolute;
      bottom: 0px;
      left: 19px;
      line-height: 14px;
      color:#999;
    }
    .yuandian-icon-blue{
      color: #26c88c;
    }
    .yuandian-icon-red{
      color: #ff0000;
    }
  }
  .selected{
    display: inline-block;
    width: 40px;
    height: 40px;
    color: #fff;
    border-radius: 70%;
    background-color: #26c88c;
    .feiji-icon{
      color: #fff;
    }
  }
  .ban{
    /*background-image: url(image/ban.jpg);*/
    background: #8ecd4a;
  }
  /*.xiu{*/
  /*!*background-image: url(./image/xiu.jpg);*!*/
  /*!*background: #f0ad4e;*!*/
  /*background-repeat: no-repeat;*/
  /*}*/
  .active {
    display: inline-block;
    width: 40px;
    height: 40px;
    color: #fff;
    border-radius: 50%;
    /*background-color: #324057;*/
    background-color: #26c88c;
    .feiji-icon{
      color: #fff;
    }

  }
  i{
    margin-right:30px;
    cursor:pointer
  }

  .other-month {
    color: #666;
    /*background: #f2f2f2;*/
  }
  .other-month.selected {
    color: #fff;
  }
</style>



